html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  background-color: #f8f8f8;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.section {
  padding: 8rem 0;
}

h1 {
  margin: 0;
  line-height: 1.5;
}

h2 {
  margin: 0;
  line-height: 1.5;
}

h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 2rem 0;
}

p {
  font-size: 2rem;
  color: #111;
}

.header {
  padding-top: 5rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}

.welcome {
  text-align: center;
}
.welcome__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  font-size: 4rem;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
}
.welcome__heading span {
  display: block;
  color: #111;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.welcome__text {
  margin: 4rem auto 0;
  max-width: 50rem;
  line-height: 1.8;
  font-size: 2.6rem;
  color: #979797;
}

@media (min-width: 768px) {
  .services__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.shedules {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url(../assets/img/backgrounds/bg_horarios.jpg);
  background-repeat: no-repeat;
  background-position: center, bottom center;
  background-size: 100%, cover;
  color: #ffffff;
}
@media (min-width: 768px) {
  .shedules {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url(../assets/img/backgrounds/bg_horarios.jpg);
    background-repeat: no-repeat;
    background-position: center, top right;
  }
}
.shedules__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .shedules__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.shedules__content {
  padding: 5rem 0;
}
.shedules__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
}
.shedules__text {
  color: #ffffff;
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}

.main-products__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  font-size: 4rem;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 5rem;
}
.main-products__heading span {
  display: block;
  color: #111;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}
.main-products__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .main-products__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .main-products__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.cite {
  padding: 5rem 0;
  background-image: url(../assets/img/backgrounds/bg_citas.jpg);
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  text-align: center;
}
.cite__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  color: #ffffff;
  font-size: 5rem;
}
.cite__text {
  margin: 0 auto;
  max-width: 60rem;
  color: #ffffff;
  line-height: 1.8;
}

.footer {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__widget {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__widget:nth-child(1) {
    align-items: flex-start;
    text-align: left;
  }
}
@media (min-width: 768px) {
  .footer__widget:nth-child(3) {
    align-items: flex-end;
    text-align: right;
  }
}
.footer__heading {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 3.4rem;
}
.footer__text {
  line-height: 1.8;
  margin: 0;
}
.footer__shedule-item {
  line-height: 1.8;
  margin-bottom: 2rem;
  font-size: 2rem;
}
.footer__copyright {
  margin: 2rem auto 0;
  padding: 2rem;
  max-width: 120rem;
  text-align: center;
  color: #979797;
  border-top: 0.1rem solid #dfdfdf;
}

[class$=__btn] {
  display: inline-block;
  margin-top: 2rem;
  padding: 2rem 2rem;
  background-color: #e082a8;
  font-size: 2.2rem;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  [class$=__btn] {
    width: auto;
  }
}
@media (hover: hover) {
  [class$=__btn]:hover {
    background-color: #d6598c;
    cursor: pointer;
  }
}

.card-service__btn {
  display: block;
  border-radius: 0;
}

.cite__btn {
  display: inline-block;
  margin-top: 2rem;
  padding: 2rem 2rem;
  background-color: #8ed3cc;
  font-size: 2.2rem;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 1rem;
  padding: 1.5rem 10rem;
}
@media (min-width: 768px) {
  .cite__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .cite__btn:hover {
    background-color: #69c5bb;
    cursor: pointer;
  }
}

.nav {
  margin: 2rem 0;
  padding: 0.5rem 0;
  background-color: #8ed3cc;
}
.nav__content {
  display: flex;
  flex-direction: column;
  border-block: 2px dashed #ffffff;
  padding: 1.5rem 0;
}
.nav__hamburguer-btn {
  margin: 0.5rem 0;
  background-color: transparent;
  color: #ffffff;
  border: none;
}
@media (min-width: 768px) {
  .nav__hamburguer-btn {
    display: none;
  }
}
.nav__hamburguer-btn svg {
  width: 3rem;
}
.nav__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  height: 0;
  overflow: hidden;
  pointer-events: none;
  transition: 0.3s all ease-in-out;
}
@media (min-width: 768px) {
  .nav__contenedor {
    flex-direction: row;
    justify-content: space-between;
    height: auto;
    overflow: visible;
    pointer-events: auto;
  }
}
.nav__contenedor--open {
  height: 35vh;
  pointer-events: auto;
}
.nav__link {
  color: #ffffff;
  font-size: 2.2rem;
}

.nav-social {
  display: flex;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .nav-social {
    margin: 0;
  }
}
.nav-social--footer {
  margin-top: 2rem;
}
.nav-social__link::before {
  content: "";
  padding: 2rem;
  margin-right: 0.5rem;
  display: inline-block;
}
.nav-social__link[href*="facebook.com"]::before {
  background-image: url(../assets/img/icons/icono_facebook.svg);
}
.nav-social__link[href*="instagram.com"]::before {
  background-image: url(../assets/img/icons/icono_instagram.svg);
}
.nav-social__link[href*="TikTok.com"]::before {
  background-image: url(../assets/img/icons/icono_tiktok.svg);
}
.nav-social__link[href*="youtube.com"]::before {
  background-image: url(../assets/img/icons/icono_youtube.svg);
}
.nav-social__accessible {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

.slider {
  position: relative;
  overflow: hidden;
}
.slider__slide {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition: transform 0.3s ease-in-out;
}
.slider__img {
  width: 100%;
}
.slider .swiper-button-next,
.slider .swiper-button-prev {
  color: #ffffff;
}

.card-service {
  margin: 0 auto 2rem;
  max-width: 35rem;
}
.card-service:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .card-service {
    max-width: unset;
    margin: 0;
  }
}
.card-service__img {
  width: 100%;
  border: 0.5rem solid #ffffff;
  transition: transform 0.3s ease-in-out;
}
@media (hover: hover) {
  .card-service__img:hover {
    transform: rotate(4deg) scale(1.2);
  }
}
.card-service__content {
  margin: 0 auto;
  max-width: 90%;
  background-color: #ffffff;
  transform: translateY(-7rem);
}
.card-service__heading {
  text-align: center;
  color: #e082a8;
  font-family: "Italianno", sans-serif;
  font-size: 4rem;
  line-height: 0.8;
  font-weight: 400;
  text-transform: lowercase;
  padding: 2rem 0;
  font-size: 3.4rem;
}
.card-service__heading span {
  display: block;
  color: #111;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}

.table-shedules {
  width: 100%;
  text-align: center;
  border: 0.5rem solid #e082a8;
  border-radius: 1rem;
  border-collapse: collapse;
}
.table-shedules__head {
  text-transform: uppercase;
  background-color: #e082a8;
}
.table-shedules__th {
  padding: 1rem;
}
.table-shedules__tr {
  transition: background-color 0.2s ease-in-out;
}
@media (hover: hover) {
  .table-shedules__tr:hover {
    background-color: #eaabc4;
  }
}
.table-shedules__td {
  padding: 1rem;
  font-size: 2.2rem;
}

.card-product {
  background-color: #ffffff;
  border-radius: 1rem;
  overflow: hidden;
}
.card-product__mask {
  overflow: hidden;
}
.card-product__img {
  display: block;
  width: 100%;
  transition: transform 0.3s ease-in-out;
}
@media (hover: hover) {
  .card-product__img:hover {
    transform: scale(1.2);
  }
}
.card-product__content {
  padding: 2rem;
}
.card-product__name {
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  font-size: 2.2rem;
  color: #e082a8;
}
.card-product__desc {
  font-size: 1.6rem;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.8;
}
.card-product__price {
  margin: 0;
  font-size: 2.6rem;
  text-align: center;
  color: #979797;
}